import React, { useEffect, useState } from "react";
import PopupWrapper from "../../../staff/Mainbody/FinanceManager/FeesStructure/PopupWrapper";
import style from "../../../staff/Mainbody/FinanceManager/FeesStructure/FeesStructure.module.css";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useTranslation } from "react-i18next";
import QLoader from "../../../../Loader/QLoader";
import TileWrapper from "../../../../Department/Institute/Components/exportData/TileWrapper";
import JoinFormInput from "../../../../JoiningForm/Student/Form/JoinFormInput";
import { useRaiseIssueCertificateStudent } from "../../../../hooks/member_tab/student-api";
import { useGetCertificateInstituteCharges } from "../../../../hooks/member_tab/department-api";
import StudentPaymentGateway from "../../../../optimized/UserStudentMember/OtherPayment/StudentPaymentGateway";
import { useNavigate, useParams } from "react-router-dom";
import { useStudentLeaving } from "../../../../hooks/institute/institute-staff-student-api";
const typeList = [
  {
    name: "bonafide",
    key: "Bonafide",
  },
  {
    name: "leaving",
    key: "Leaving",
  },
  {
    name: "transfer",
    key: "Transfer",
  },
  // {
  //   name: "migration",
  //   key: "Migration",
  // },
];
const originalList = [
  {
    name: "original_copy",
    key: true,
  },
  {
    name: "dublicate_copy",
    key: false,
  },
];
const RaiseStudentCertificateRequest = ({
  studentId,
  instituteId,
  onRefetch,
  onClose,
  whichTab,
}) => {
  const { t } = useTranslation();
  const [filled, setFilled] = useState("");
  const [disabled, setDisabled] = useState(false);
  const params = useParams();
  const navigate = useNavigate();
  const [state, setState] = useState({
    query_content: "",
    certificate_type: "Bonafide",
    is_original: true,
  });
  const [isGateway, setIsGateway] = useState(false);
  const [raiseIssueCertificateStudent] = useRaiseIssueCertificateStudent();
  const [studentLeaving] = useStudentLeaving();

  const {
    getCertificateInstituteCharges,
    getCertificateInstituteChargesRefetch,
  } = useGetCertificateInstituteCharges({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });

  useEffect(() => {
    if (instituteId) {
      getCertificateInstituteChargesRefetch();
    }
  }, [instituteId, getCertificateInstituteChargesRefetch]);

  useEffect(() => {
    if (whichTab) {
      setState((prev) => ({
        ...prev,
        certificate_type: whichTab,
      }));
    }
  }, [whichTab]);
  const onInputChange = (e) => {
    if (e.target.value?.length <= 75) {
      setState((prev) => ({
        ...prev,
        query_content: e.target.value,
      }));
    }
  };
  const onCloseModal = () => {
    if (disabled) {
    } else {
      onClose();
    }
  };
  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      certificate_type: val,
    }));
  };
  const onSelectOriginal = (val) => {
    setState((prev) => ({
      ...prev,
      is_original: val,
    }));
  };

  const onConfirm = () => {
    if (!state.query_content) {
      setFilled({
        query_content: "* required",
      });
    } else {
      if (studentId && instituteId) {
        if (
          getCertificateInstituteCharges?.certificate?.certificate_fund_charges
            ?.bona_charges > 0 ||
          getCertificateInstituteCharges?.certificate?.certificate_fund_charges
            ?.leaving_charges > 0 ||
          getCertificateInstituteCharges?.certificate?.certificate_fund_charges
            ?.transfer_charges > 0
        ) {
          setIsGateway(true);
        } else {
          setDisabled((pre) => !pre);
          raiseIssueCertificateStudent({
            id: instituteId,
            sid: studentId,
            issueCertificate: state,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch({});
        }
      }
    }
  };

  const onFillForm = (value) => {
    if (
      getCertificateInstituteCharges?.certificate
        ?.certificate_leaving_form_student === "Yes"
    ) {
      setDisabled((pre) => !pre);
      studentLeaving({
        id: instituteId,
        gr: studentId,
        leavingFeedback: {
          accessFrom: "Student",
          leaving_certificate_attach: "",
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          navigate(`/q/${params.username}/student/certificate/form`, {
            state: {
              student: res.data.student,
              download: res.data.download,
              instituteId: instituteId,
              is_leaving: value === "Leaving" ? "leaving" : "transfer",
              extraData: {
                accessFrom: "Student",
                whichTab: value,
              },
            },
          });
        })
        .catch({});
    }
  };

  // console.info(
  //   "getCertificateInstituteCharges",
  //   getCertificateInstituteCharges
  // );
  // getCertificateInstituteCharges?.certificate?.certificate_leaving_form_student
  return (
    <>
      {isGateway ? (
        <StudentPaymentGateway
          onClose={() => setIsGateway(false)}
          instituteId={instituteId}
          studentId={studentId}
          certificateType={state.certificate_type}
          certificateContent={state.query_content}
          certificateCharges={
            state.certificate_type === "Bonafide"
              ? getCertificateInstituteCharges?.certificate
                  ?.certificate_fund_charges?.bona_charges
              : state.certificate_type === "Leaving"
              ? getCertificateInstituteCharges?.certificate
                  ?.certificate_fund_charges?.leaving_charges
              : state.certificate_type === "Transfer"
              ? getCertificateInstituteCharges?.certificate
                  ?.certificate_fund_charges?.transfer_charges
              : 0
          }
          isOriginal={state.is_original}
        />
      ) : (
        <PopupWrapper onClose={onCloseModal}>
          <div
            className={style.add_category_modal}
            style={{
              width: "35rem",
              minHeight: "10rem",
              height: "fit-content",
            }}
          >
            <section
              className={style.fee_middle_section_container}
              style={{ marginBottom: "1.2rem" }}
            >
              <h6
                style={{
                  fontSize: "20px",
                  fontWeight: "600",
                }}
              >
                {t("certificate_request")}
              </h6>

              <BorderBottom
                customStyle={{
                  width: "100%",
                  marginTop: "0.7rem",
                }}
              />

              <TileWrapper
                labelText="certificate_type"
                tileList={typeList}
                activeTile={state.certificate_type}
                onTile={onSelectType}
              />
              {getCertificateInstituteCharges?.certificate
                ?.certificate_leaving_form_student === "Yes" &&
              (state.certificate_type === "Leaving" ||
                state.certificate_type === "Transfer") ? null : (
                <JoinFormInput
                  labelText={t("enter_reason")}
                  placeholder={t("enter_reason")}
                  name={"query_content"}
                  value={state.query_content}
                  type={"text"}
                  onChange={onInputChange}
                  errorShow={filled["query_content"]}
                />
              )}

              {state.certificate_type === "Leaving" ||
              state.certificate_type === "Transfer" ? (
                <>
                  {getCertificateInstituteCharges?.certificate
                    ?.certificate_leaving_form_student === "Yes" ? null : (
                    <TileWrapper
                      labelText="certificate_copy"
                      tileList={originalList}
                      activeTile={state.is_original}
                      onTile={onSelectOriginal}
                    />
                  )}
                </>
              ) : null}
              {state.certificate_type === "Leaving" ||
              state.certificate_type === "Transfer" ? (
                <>
                  {getCertificateInstituteCharges?.certificate
                    ?.certificate_leaving_form_student === "Yes" ? (
                    <button
                      onClick={() => onFillForm(state.certificate_type)}
                      className={style.fee_middle_confirm_button}
                      style={{
                        marginTop: "2rem",
                      }}
                    >
                      {t("fill_form")}
                    </button>
                  ) : (
                    <button
                      onClick={onConfirm}
                      className={style.fee_middle_confirm_button}
                      style={{
                        marginTop: "2rem",
                      }}
                    >
                      {state.certificate_type === "Bonafide"
                        ? getCertificateInstituteCharges?.certificate
                            ?.certificate_fund_charges?.bona_charges > 0
                          ? `${t("confirm_and_pay")} (${t("rs")}. ${
                              getCertificateInstituteCharges?.certificate
                                ?.certificate_fund_charges?.bona_charges
                            })`
                          : t("confirm")
                        : state.certificate_type === "Leaving"
                        ? getCertificateInstituteCharges?.certificate
                            ?.certificate_fund_charges?.leaving_charges > 0
                          ? `${t("confirm_and_pay")} (${t("rs")}. ${
                              getCertificateInstituteCharges?.certificate
                                ?.certificate_fund_charges?.leaving_charges
                            })`
                          : t("confirm")
                        : state.certificate_type === "Transfer"
                        ? getCertificateInstituteCharges?.certificate
                            ?.certificate_fund_charges?.transfer_charges > 0
                          ? `${t("confirm_and_pay")} (${t("rs")}. ${
                              getCertificateInstituteCharges?.certificate
                                ?.certificate_fund_charges?.transfer_charges
                            })`
                          : t("confirm")
                        : null}
                    </button>
                  )}
                </>
              ) : (
                <button
                  onClick={onConfirm}
                  className={style.fee_middle_confirm_button}
                  style={{
                    marginTop: "2rem",
                  }}
                >
                  {state.certificate_type === "Bonafide"
                    ? getCertificateInstituteCharges?.certificate
                        ?.certificate_fund_charges?.bona_charges > 0
                      ? `${t("confirm_and_pay")} (${t("rs")}. ${
                          getCertificateInstituteCharges?.certificate
                            ?.certificate_fund_charges?.bona_charges
                        })`
                      : t("confirm")
                    : null}
                </button>
              )}
            </section>
          </div>
          {disabled && <QLoader />}
        </PopupWrapper>
      )}
    </>
  );
};

export default RaiseStudentCertificateRequest;
